/* ========================================================= */
/*                Special Styling Class Defaults             */
/* ========================================================= */

.hidden {
   opacity:0;
}

/*** HOVER CHILD ***/

.toggleMyChild .hiddenChild { 
  display:none; 
} 
.fadeMyChild .hiddenChild { 
  opacity:0; 
  visibility:hidden; 
  -webkit-transition: all 300ms;
  -moz-transition: all 300ms;
  transition: all 300ms;
} 


/** Popup Styling **/

.blackBack { 
  z-index:1500; 
  position:fixed;
  top:0;
  left:0;
  width:100%;
  height:100%;
  background:rgba(0,0,0,0.6);
  display:none;
}

.popup { 
  z-index:1600;
  overflow:auto;
  position:absolute;
  left:0;
  top:0;
  display:none;
  background: #fff;
  padding:20px;
  max-width:100%;
}
a.closeX { 
  position:absolute; 
  top:0;
  right:0;
  cursor:pointer;
  text-decoration:none;
}

/*** Video Container ***/

.videoContainer {
	position: relative;
	padding-bottom: 54.25%; 
	padding-top: 25px;
	height: 0;
}
.videoContainer iframe {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}


/** Form Styling **/

.fullwidthForm select, .fullwidthForm input, .fullwidthForm textarea {
  width:100%;
  margin-bottom:5px;
}
.fullwidthForm input[type="submit"] {
  width:auto; 
}
.largeForm select, .largeForm input, .largeForm textarea, .largeForm button, .largeForm .button {
  padding-top:12px;
  padding-bottom:12px;
  margin-top:5px;
  margin-bottom:5px;
}
.button.large, .input[type="submit"].large, .largeForm .button, .largeForm input[type="submit"] {
  padding-left:20px;
  padding-right:20px;
}
.largeForm label {
  display:inline-block;
  padding-top:5px;
}
.niceSelect {
  border-radius:3px;
  background:url(images/arrow-dark.png) right 10px center no-repeat;
  -webkit-appearance: none; 
}


/** Nice Table **/
.niceTable table, table.niceTable { 
  border:1px solid #e8e8e8;
}
.niceTable thead { 
  width:100%;
  background:#f5f5f5;
}
.niceTable thead th { 
  text-align:left;
  padding:10px 5px;
  font-weight:normal;
}
.niceTable tbody, .niceTable tfoot { 
  background:#fff; 
}
.niceTable tbody td {
  padding:17px 5px;
  border-bottom:1px solid #e8e8e8;
}

.niceTable tfoot td { 
  padding:2px 5px; 
  
}


        

