/* HTML5 Elements */
article, aside, details, figcaption, figure, dialog, footer, header, hgroup, menu, nav, section {
  display: block; }

/** General Typography **/
h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0; }

/** Buttons & Forms **/
select, input, textarea, button, .button {
